@charset "utf-8";

/* -----------------------------------------------------------------------
## Button styles #########################################################
----------------------------------------------------------------------- */
.snsBtns {
	text-align: center;
	&_item {
		display: inline-block;
		margin: 0 5px;
		a {
			position: relative;
			display: inline-block;
			line-height: 1;
			width: 44px;
			height: 44px;
			border-radius: 1000px;
			background: $color_bg_light;
			color: $color_text_dark;
			text-align: center;
			@include fontsize(20);
		}
		.fa {
			line-height: 44px;
			@include fontsize(26);
		}
	}
}

/* -----------------------------------------------------------------------
## Ancor Styles ##########################################################
----------------------------------------------------------------------- */
a.ani-uLine {
	&:hover {
		text-decoration: underline;
	}
}
a.ani-opacity {
	transition: opacity .4s ease;
	&:hover {
		opacity: .7;
	}
}
a.ani-uBar {
	position: relative;
	text-shadow: 0 0 5px rgba(0,0,0,.5);
	&:after {
		position: absolute;
		bottom: -2px;
		left: 0;
		right: 0;
		display: inline-block;
		width: 100%;
		height: 2px;
		margin: auto;
		background: #fff;
		content: "";
		transform: scale(0, 1);
		transition: transform .2s $easeOutQuint, -webkit-transform .2s $easeOutQuint;
	}
	&:hover:after {
		transform: scale(1, 1);
	}
}
a.ani-reverseBtn {
	position: relative;
	display: inline-block;
	line-height: 44px;
	padding: 0 20px 0 20px;
	border-radius: 1000px;
	background: transparent;
	color: $color_text;
	text-align: center;
	transition: all .6s ease;
	&:hover {
		background: $color_bg_light;
		color: $color_text_dark;
	}
}

@media screen and (max-width: 768px) {
	a.ani-uBar {
		display: block;
		position: relative;
		text-shadow: none;
		padding: 14px 10px;
		@include fontsize(14);
		font-weight: normal;
		&:after {
			display: none;
		}
		&:hover:after {
			transform: none;
		}
	}
}

/* -----------------------------------------------------------------------
## visualBox #############################################################
----------------------------------------------------------------------- */
.visualBox {
	&_title {
		text-align: center;
		font-family: 'Archivo Black', sans-serif;
		@include fontsize(40);
	}
	&_img {
		margin-top: 16px;
		img {
			width: 100%;
			height: auto;
		}
	}
	&_copy {
		margin-top: 16px;
		font-size: 1.5rem;
	}
	&_text {
		margin-top: 16px;
	}
}

@media screen and (max-width:768px) {
	.visualBox {
		&_copy {
			font-size: 1rem;
		}
		&_text {
			@include fontsize(14);
		}
	}
}

/* -----------------------------------------------------------------------
## slick setting #########################################################
----------------------------------------------------------------------- */
.box:focus {
	outline: none;
}
.slick-next, .slick-prev {
	width: 70px;
	height: 70px;
	z-index: 2;
	cursor: pointer;
}
.slick-next:before, .slick-prev:before {
	font-size: 70px;
	opacity: 1;
	transition: opacity .6s ease;
}
.slick-next:hover:before, .slick-prev:hover:before {
	opacity: .75;
}
.slick-prev {
	left: -96px;
}
.slick-next {
	right: -96px;
}

@media screen and (max-width:768px) {
	.slick-next, .slick-prev {
		top: -24px;
		width: 44px;
		height: 44px;
		&:before {
			font-size: 44px;
		}
	}
	.slick-next {
		right: 0;
	}
	.slick-prev {
		left: 0;
	}
}


/* -----------------------------------------------------------------------
## columns ###############################################################
----------------------------------------------------------------------- */
.col2Box {
	display: flex;
	margin-left: -48px;
	&_item {
		flex: 1;
		margin-left: 48px;
	}
}
.col3Box {
	display: flex;
	margin-left: -32px;
	&_item {
		flex: 1;
		margin-left: 32px;
	}
}
.col4Box {
	display: flex;
	margin-left: -16px;
	&_item {
		flex: 1;
		margin-left: 16px;
	}
}
@include mediaQuery(sp){
	.col2Box {
		display: block;
		margin-left: 0;
		&_item {
			margin-left: 0;
		}
	}
	.col3Box {
		display: block;
		margin-left: 0;
		&_item {
			margin-left: 0;
		}
	}
	.col4Box {
		display: block;
		margin-left: 0;
		&_item {
			margin-left: 0;
		}
	}
}

/* -----------------------------------------------------------------------
## mainSubBox ############################################################
----------------------------------------------------------------------- */
.mainSubBox {
	display: flex;
	&_main {
		flex: 4;
	}
	&_sub {
		flex: 1;
	}
}
@include mediaQuery(sp){
	.mainSubBox {
		display: block;
		&_main {
			flex: 4;
		}
		&_sub {
			flex: 1;
		}
	}
}