.pageFooter {
	background: $color_bg;
}
.footerInner {
	width: 1000px;
	margin: 0 auto;
	padding: 48px 0 16px;
	transition: -webkit-filter .8s ease, transform .8s ease;
	.logo {
		float: left;
		width: 75px;
		margin: 0 15px 0 0;
		img {
			width: 100%;
		}
	}
	.contact {
		float: left;
		padding: 4px 0;
		@include fontsize(10);
	}
	.snsBtns {
		float: right;
		&_item {
			margin: 0;
			a {
				width: 24px;
				height: 24px;
			}
			.fa {
				line-height: 24px;
				@include fontsize(16);
			}
		}
	}
	.footerNavi {
		margin: 8px 0 0;
		border-left: 1px solid $color_line;
		&_box {
			float: left;
			width: 142px;
			padding: 0 0 0 6px;
			border-right: 1px solid $color_line;
		}
		&_title {
			font-weight: bold;
			@include fontsize(13);
		}
		&_list_item {
			margin: 4px 0;
			padding: 0 4px;
			@include fontsize(11);
		}
		a:hover {
			text-decoration: underline;
		}
	}
}

@media screen and (max-width: 768px){
	.footerInner {
		width: auto;
		padding: 16px 0 0;
		transition: -webkit-filter .8s ease, transform .8s ease;
		.logo {
			float: none;
			width: 75px;
			margin: 0 15px 0 0;
			padding: 0 10px;
			img {
				width: 100%;
			}
		}
		.contact {
			float: none;
			padding: 16px 10px 0;
			@include fontsize(10);
		}
		.snsBtns {
			float: none;
			padding: 1em 10px;
			text-align: center;
			&_item {
				a {
					width: 44px;
					height: 44px;
				}
				.fa {
					line-height: 44px;
					@include fontsize(24);
				}
			}
		}
		.footerNavi {
			margin: 8px 0 0;
			border-left: none;
			border-top: 1px solid $color_line;
			&_box {
				float: none;
				width: auto;
				padding: 0;
				border-right: none;
			}
			&_title {
				background: rgba(255,255,255,.25);
				border-bottom: 1px solid $color_line;
				font-weight: bold;
				@include fontsize(16);
				a {
					display: block;
					padding: 12px 10px;
				}
			}
			&_list {
				display: none;
			}
			&_title {
				position: relative;
				&:after {
					display: block;
					position: absolute;
					top: 0;
					bottom: 0;
					right: 10px;
					width: 1em;
					height: 1em;
					line-height: 1;
					margin: auto;
					text-align: center;
					content: "\f107";
					font-weight: normal;
					font-size: 1.5rem;
					font-family: FontAwesome;
					transition: transform .4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
				}
				&.open:after {
					transform: rotate(-180deg);
				}
			}
			.recruit .footerNavi_title {
				&:after {
					content: "\f105";
					font-family: FontAwesome;
				}
			}
			&_list_item {
				margin: 0;
				padding: 0;
				border-bottom: 1px solid $color_line;
				@include fontsize(14);
				a {
					display: block;
					padding: 12px 10px;
				}
			}
			a:hover {
				text-decoration: none;
			}
		}
	}

}