@charset "utf-8";

* {
  box-sizing: border-box;
}
html,
body {
  min-width: 1000px;
  width: 100%;
  margin: 0;
  padding: 0;
}
body {
  background: $color_bg;
  font: 16px/1.4 Verdana, Arial, "メイリオ", Meiryo, "ヒラギノ角ゴPro W3", "Hiragino Kaku Gothic Pro", "ＭＳ Ｐゴシック", sans-serif;
  color: $color_text;
}
* html body {
  font-size: small;
}
*:first-child + html body {
  font-size: small;
}
a {
  color: $color_link;
  text-decoration: none;
}
a[target="_blank"] {
  &:after {
    display: inline;
    content: " \f08e";
    font-family: FontAwesome;
  }
}
ul,
ol {
  list-style: none;
}
.clearfix:after {
  display: block;
  clear: both;
  content: ""
}
select {
  &:focus {
    outline: none;
  }
}


/* sections */
.section {
  position: relative;
  overflow: hidden;
  min-height: 900px;
  padding: 64px 0;
  &Title {
    text-align: center;
    font-family: 'Archivo Black', sans-serif;
    @include fontsize(40);
  }
  &SubTitle {
    line-height: 1;
    text-align: center;
    font-weight: bold;
    padding: 16px 0;
    font-family: 'Archivo Black', sans-serif;
    @include fontsize(24);
  }
  &Inner {
    position: relative;
    z-index: 1;
    width: 1000px;
    height: 100%;
    margin: 0 auto;
    padding: 32px 64px;
    background: rgba(0,0,0,.75);
  }
  .contentsTitle {
    margin-top: 64px;
    text-decoration: underline;
    text-align: center;
    @include fontsize(24);
  }
  .bg {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height:120%;
    img {
      position: relative;
      left: 50%;
      transform: translate(-50%, 0);
    }
  }
}

@media screen and (max-width: 768px) {
  html,
  body {
    min-width: 0;
    width: 100%;
    margin: 0;
    padding: 0;
  }
  .section {
    position: relative;
    height: auto;
    min-height: 0;
    &Inner {
      width: 100%;
      padding: 16px 10px 32px;
    }
    &Title {
      @include fontsize(24);
    }
    .contentsTitle {
      margin-top: 32px;
      @include fontsize(18);
    }
  }
}
