/* ################# Toplayer ################# */
.menuBtn {
	display: none;
}
.pageHeader {
	.gNavi {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 9999;
		width: 100%;
	}
	.gNaviList {
		transition: background .75s ease;
		&.on {
			background: rgba(0,0,0,.75);
		}
		&_item {
			float: left;
			width: 20%;
			padding: 12px 0;
			text-align: center;
			font-weight: bold;
			font-size: 1.5rem;

		}
	}
}
.overlay {
	display: none;
}

@media screen and (max-width: 1200px){
	.pageHeader {
		.gNaviList_item {
			font-size: 1.2rem;
		}
	}
}
@media screen and (max-width: 960px){
	.pageHeader {
		.gNaviList_item {
			font-size: .8rem;
		}
	}
}
@media screen and (max-width: 768px){
	.menuBtn {
		position: fixed;
		top: 6px;
		right: 6px;
		z-index: 9999;
		display: block;
		width: 44px;
		height: 44px;
		border-radius: 3px;
		background: rgba(255,255,255,.5);
		font-size: 2rem;
		text-align: center;
		line-height: 44px;
	}
	.pageHeader {
		.gNavi {
			position: fixed;
			top: 56px;
			left: 100%;
			z-index: 9999;
			width: 100%;
			background: rgba(255,255,255,.75);
			transition: transform .75s $easeOutQuint;
			transform: translate(0, 0);
		}
		.gNaviList {
			display: block;
			border-top: 1px solid $color_line_dark;
			&_item {
				float: none;
				width: 100%;
				padding: 0;
				border-bottom: 1px solid $color_line_dark;
				text-align: left;
				font-weight: bold;
				font-size: 1.5vw;
			}
			a {
				color: $color_text_dark;
			}
			&.on {
				background: transparent;
			}
		}
	}
	.overlay {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1000;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,.75);
	}
	.onMenu {
		.pageHeader {
			.gNavi {
				transform: translate(-100%, 0);
			}
		}
		.fa-cog {
			animation: rotation 5s linear infinite;
		}
	}
	@keyframes rotation{
		0%  {transform: rotate(0deg);}
		100%{transform: rotate(360deg);}
	}
}


/* ################# Underlayer ################# */
.underlayerHeader {
	position: relative;
	width: 1000px;
	margin: 0 auto;
	.titleBox {
		height: 66px;
		padding: 8px;
	}
	.siteName {
		float: left;
		@include fontsize(14);
	}
	.logo {
		width: 75px;
		margin: 0 auto;
	}
	.snsBtns {
		position: absolute;
		top: 8px;
		right: 0;
		&_item {
			margin: 0;
		}
		&_item a {
			width: 30px;
			height: 30px;
		}
		&_item .fa {
			@include fontsize(18);
			line-height: 30px;
		}
	}
	.gNavi {
		height: 48px;
		&List {
			&_item {
				display: inline-block;
				width: 16.52%;
				margin: 0 0 0 4px;
				background: rgba(0,0,0,.75);
				&:first-child {
					margin-left: 0;
				}
				&:nth-child(4){
					width: 30%;
				}
			}
			.active {
				background: rgba(255,255,255,.75);
				a {
					color: $color_text_dark;
				}
			}
			a {
				display: block;
				border: 2px solid $color_line;
				border-radius: 0;
				padding: 0 10px;
				text-align: center;
			}
		}
	}
	&.float {
		.titleBox {
			margin-bottom: 66px;
		}
		.gNavi {
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			z-index: 9999;
			&List {
				&_item {
					margin: 0;
					float: left;
					width: 17.5%;
				}
				a {
					border: none;
					border-left: 1px solid $color_line;
				}
				&_item:first-child {
					a {
						border-left: none;
					}
				}
				&_item:nth-child(4){
					width: 30%;
				}
			}
		}
	}
}
@media screen and (max-width: 768px){
	.underlayerHeader {
		width: auto;
		.titleBox {
			display: block;
			height: auto;
			margin-bottom: 0;
		}
		.siteName {
			float: right;
			padding-top: 8px;
			padding-right: 55px;
			@include fontsize(12);
		}
		.logo {
			float: left;
		}
		.snsBtns {
			position: static;
			clear: both;
			padding-top: 16px;
		}
		.gNavi {
			position: fixed;
			top: 56px;
			left: 100%;
			z-index: 9999;
			width: 100%;
			transition: transform .75s $easeOutQuint;
			transform: translate(0, 0);
		}
		&.float {
			.gNavi {
				top: 56px;
				left: 100%;
			}
			.gNaviList {
				&_item {
					float: none;
					width: 100%;
					&:nth-child(4) {
						width: 100%;
					}
				}
			}
		}
		.gNaviList {
			display: block;
			border-top: 1px solid $color_line_dark;
			&_item {
				float: none;
				width: 100%;
				margin: 0;
				padding: 0;
				border-bottom: 1px solid $color_line_dark;
				background: rgba(255,255,255,.75);
				text-align: left;
				font-weight: bold;
				&:nth-child(4) {
					width: 100%;
				}
			}
			a {
				width: 100%;
				color: $color_text_dark;
				border: none;
				font-weight: normal;
				text-align: left;
				@include fontsize(12);
			}
		}
	}
	.onMenu {
		.underlayerHeader {
			.gNavi {
				transform: translate(-100%, 0);
			}
		}
	}
}
/* ################# Breadcrumbs ################# */
.breadcrumbs {
	width: 1000px;
	margin: 8px auto;
	@include fontsize(12);
	li {
		display: inline-block;
		&:after {
			content: "/";
			margin-left: .5em;
			margin-right: .3em;
		}
		&:last-child:after {
			content: "";
		}
	}
}
@media screen and (max-width: 768px){
	.breadcrumbs {
		width: auto;
		padding: 0 8px;
	}
}